import { icoinfos } from "./icondata";


const icoinfo =icoinfos

// Function to capitalize the first letter of each word
const capitalizeWords = (str) => {
  return str.split('-').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ');
};

const convertCssToIconArray = (cssContent) => {
  const iconRules = cssContent.match(/\.icon-(.+?):before\s*{\s*content:\s*"\\(.+?)";\s*}/g) || [];
  const icons = iconRules.map((rule) => {
    const matches = rule.match(/\.icon-(.+?):before\s*{\s*content:\s*"\\(.+?)";\s*}/);
    if (matches) {
      const [, iconName, unicode] = matches;
      const formattedIconName = capitalizeWords(iconName);
      return {
        className: `icon-${iconName}`,
        iconName: formattedIconName,
        unicode,
      };
    }
    return null;
  }).filter(Boolean);

  return icons;
};

export const iconsArray = convertCssToIconArray(icoinfo).map(icon => ({ className: icon.className, iconName: icon.iconName, unicode: icon.unicode }));

